	module.exports = {"data":{"wordpressPage":{"date":"2018-02-06T12:58:33.000Z","title":"Home","content":"<blockquote><p>&#8220;Pass the peas like they used to say&#8221;</p>\n<p><span style=\"color: #333333; font-size: 1rem;\">JB &#8211; Food for thought 1972</span></p></blockquote>\n<p><em>&#8220;Comfort Food is meer dan alleen lekker eten. Het gaat om verbondenheid met familie, vrienden en gelijkgestemden in een relaxte sfeer met goede muziek.&#8221;</em></p>\n<h3 id=\"overons\">Over ons</h3>\n<p>Eten, muziek en mensen. Pass the Peas mobiele catering brengt deze samen. De geur van vers gebakken kip, met op de achtergrond soul, jazz, funk of hip hop. Of je nu gemoedelijk in een schommelstoel heen en weer deint, languit op kussens ligt of aan een picknicktafel zit met je vrienden: bij Pass the Peas kan je even Ã©cht relaxen, tijdens een feest, festival of food market.</p>\n<p>De mogelijkheden van ons menu zijn onbeperkt en het meest trots zijn we op onze specialiteit: verse, sappige en krokant gebakken kip. Wat ons betreft hÃ©t ultieme comfort food: eten waar je blij van wordt. Soul Food uit het zuiden van de VS is onze inspiratie. Na continu geduldig experimenteren, proeven en aanpassen hebben we er een Caribische twist aan gegeven. Het resultaat is kruidig, krokant en smaakvol tot de kern. Bij de kip serveren we coleslaw en huisgemaakte salsa. Wat ons betreft comfort food zoals comfort food bedoeld is. En dat proef je!</p>\n"}},"pathContext":{}}

